import "./Modal.css"

import sliderImage1 from "../../assets/slider/thumbnail_sl4.jpg";
import sliderImage2 from "../../assets/slider/thumbnail_sl3.jpg";
import sliderImage3 from "../../assets/slider/thumbnail_sl2.jpg";
import sliderImage4 from "../../assets/slider/thumbnail_sl1.jpg";

import { useEffect, useState } from "react";

const AboutModal = () => {
    const [currentSlide, setCurrentSlide] = useState(0);

    const images = [
        sliderImage1,
        sliderImage2,
        sliderImage3,
        sliderImage4,
      ];

      useEffect(() => {
        const interval = setInterval(() => {
          setCurrentSlide((prevSlide) => (prevSlide + 1) % images.length);
        }, 4000);
      
        return () => clearInterval(interval);
      }, [currentSlide, images.length]);

    return (
        <div className="modal-window">
            <div className="modal-content">
                <img
                    src="https://cdn-icons-png.flaticon.com/128/130/130882.png"
                    alt="back-icon"
                    className="back-icon"
                    onClick={() => window.location.reload()}
                />

                <div className="about-title">
                    <h1>O szkoach</h1>
                </div>

                <div className="about-slider">
                    {images.map((image, index) => (
                        <img
                        key={index}
                        src={image}
                        alt={`slide-${index}`}
                        className={index === currentSlide ? 'slide active' : 'slide'}
                        />
                    ))}
                </div>
                
                <div className="about-text">
                    <p>Szkoa mieci si przy ulicy Koobrzeskiej 9. Pooona jest w parku, z dala od ruchliwych ulic. Szczelne ogrodzenie oraz system monitoringu skadajcy si z 80 kamer gwarantuje bezpieczestwo modziey. Baza sportowa to kilka duych boisk ze sztuczn nawierzchni, boisko trawiaste, 3 sale gimnastyczne oraz pikne korty tenisowe  wszystko otoczone zieleni. Lokalizacja szkoy to centrum miasta  dojazd zatem jest atwy</p>
                    <p>W cigu kilku ostatnich lat zaszy ogromne zmiany zarwno w wygldzie, jak i wyposaeniu szkoy. Jest ona obecnie obiektem najnowoczeniejszym, bardzo dobrze wyposaonym w najnowsze pomoce dydaktyczne. Wszechobecne komputery, projektory, 86 calowe ekrany dotykowe, sie internetowa, serwery i dziennik elektroniczny z powodzeniem wspomagaj prac i nauk.</p>
                    <p>W eleganckiej stowce szkolnej codziennie spoywa posiki ponad 550 uczniw. W szkole dziaa Szkolny Orodek Kariery, w ktrym doradca zawodowy pomaga podj decyzj dotyczc dalszego ksztacenia. Szkolne Centrum Psychologiczno-Pedagogiczne, klub sportowy, szkoa nauki jazdy uzupeniaj ofert szkoy, zaspokajaj pozalekcyjne potrzeby naszych uczniw.</p>
                    <p>Powodem do dumy s nowoczesne pracownie i sale lekcyjne. Oddany do dyspozycji sprzt naley do najnowoczeniejszego na rynku komputerowym, cieszy nauczycieli i uczniw.</p>
                    <p>We wspczesnym wiecie dobrze wyksztacony mody czowiek musi zna jzyki obce. Szkoa gwarantuje profesjonalne ksztacenie kompetencji jzykowych. Wszyscy ucz si j. angielskiego i drugiego jzyka obcego do wyboru (niemiecki, hiszpaski, francuski). Wymiany uczniw, zagraniczne obozy jzykowe, udzia w Erasmus+ otwieraj naszym uczniom drog na wiat.</p>
                    <p className="last-text">Jestemy po to, by wprowadzi naszych uczniw w wiat wiedzy i dobrze przygotowa ich do ycia, aby mogli odnie swj pierwszy yciowy SUKCES!</p>
                </div>
            </div>
        </div>
    );
}

export default AboutModal;